* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ol li {
  font-weight: bold;
  list-style-type: decimal;
  list-style: decimal;
  list-style-position: inside;
  padding-left: 25px;
}
ul li {
  font-weight: normal;
  list-style: disc;
  margin-left: 50px;
  padding-left: 5px;
}
